@use "var";

@media screen and (min-width: 768px){}


.footer {
    padding: 0;
    background: linear-gradient(to bottom, #78686C, #625559);
    max-height: 30vh;


    &__content {
        display: grid;
        grid-template-columns: 1fr auto 1fr;
        grid-template-rows: 1fr;
        gap: var.$spacer-s;
        position: relative;
        margin-top: -8px;
        z-index: 1;
    }

    &__icons {
        display: flex;
        justify-content: flex-end;
        gap: 8px;

        grid-column: 1/2;
        grid-row: 1/2;
        justify-self: end;
        align-self: end;
        padding: 0 0 var.$spacer-s 0;
        width: 100%;
       
    }

    &__img {
        height: 20vh;
        grid-column: 2/3;
        grid-row: 1/2;
        
    }

    &__text {
        font-family: var.$font-logo;
        color: var.$color-white;
        font-size: var.$font-size-m;

        grid-column: 3/4;
        grid-row: 1/2;
        justify-self: start;
        align-self: end;
        padding: 0 0 var.$spacer-s 0;
        width: 100%;
    }
}